
body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}


main {
  text-align: center;
  margin-top: 40px;
}
header {
  margin: 0;
  height: 56px;
  padding: 0 16px 0 24px;
  background-color: #35495E;
  color: #ffffff;
  text-align: center;
}
header span {
  display: block;
  position: relative;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 16px;
}
button {
  background: #51B767;
  color: #ffffff;
  padding: 15px;
  border-radius: 0;
  font-weight: bold;
  font-size: 15px;
  border: 0;
}
.cards {
  background: #F5F5F5;
  height:150px;
}
.cards:hover {
  -webkit-transform: translateY(-0.5em);
          transform: translateY(-0.5em);
  background: #EBEBEB;
}
.cards {
  -webkit-column-count: 1;
          column-count: 1;
  -webkit-column-gap: 1em;
          column-gap: 1em;
  margin-top: 70px;
}
img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    //height: 40px;
}

